import { mockProducts } from "@/app/data/mockProducts"
import Image from "next/image"


interface Props {
  params: { productId: string }
}

export default function ProductDetailPage({ params }: Props) {
  const product = mockProducts.find((p) => p.id === params.productId)

  if (!product) {
    return <div className="container mx-auto px-4 py-8">Product not found.</div>
  }

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex flex-col md:flex-row gap-6">
        <Image src={product.image} alt={product.name} className="w-full md:w-1/2 h-auto rounded-lg" />
        <div>
          <h1 className="text-3xl font-bold">{product.name}</h1>
          <p className="text-gray-600 text-lg mt-2">${product.price}</p>
          <p className="mt-4">{product.description}</p>
          <button className="mt-6 bg-primary text-white px-6 py-3 rounded-lg">Add to Cart</button>
        </div>
      </div>
    </div>
  )
}
